import React from 'react';
import { useSelector } from 'react-redux'

const WhoseTurn = () => {

  const { myTurn, soon } = useSelector(state => state.switchHand)

  return (
    <div>
      {soon}
      {
        myTurn
        ? soon===17 ? <h2> .     .</h2> : <h2> .</h2>
        : <h2> .</h2>
      }
    </div>
  );
};

export default WhoseTurn;