import './App.css';
import React, { useEffect, useReducer, useState } from 'react';
import { useSelector } from 'react-redux'
import { Helmet } from 'react-helmet';
import HowToPlay from '../components/Buttons/HowToPlay'
import StartButton from '../components/Buttons/StartButton';
import DecideButton from '../components/Buttons/DecideButton';

import Darkmode from '../components/Buttons/Darkmode'
import CardList from './CardList'
import Dora from './Dora'
import MyHand from './MyHand'
import DecidedHand from './DecidedHand'
import DecidedField from './DecidedField'
import MyDiscard from './MyDiscard'
import OpponentDiscard from './OpponentDiscard'
import WebSocket from '../components/WebSocket'
import ScoreBoard from '../components/ScoreBoard'
import Lobby from '../components/Lobby'
import WhoseTurn from '../components/WhoseTurn'
import Ron from '../components/Buttons/Ron'
import Result from '../containers/Result'
import WaitBackdrop from '../components/WaitBackdrop'

const App = () => {

  const background = useSelector(state => state.enableDarkMode.background)
  const { phase, time, roomID } = useSelector(state => state.switchHand)
  // const cards = useSelector(state => state.switchHand.cards)
  // const dispatch = useDispatch()

  const [connected, setConnect] = useState(false)
  const [, forceUpdate] = useReducer(x => x + 1, 0);

  useEffect(() => forceUpdate(), [time])

  useEffect( async () => {
    // const connectResponse = await fetch('http://localhost:3000/')
    const connectResponse = await fetch('https://intense-brushlands-31556.herokuapp.com/')
    const connectResponseJson = connectResponse.json()
    if (connectResponseJson) {
      setConnect(true)
    }
  }, [])

  const renderSwitch = (phase) => {
    switch (phase) {

      case 0: // ,  
        return (
          <div>
            <div className='routeTest'></div>
            {connected ? '.' : '  ..'}
            <Lobby />
            <StartButton />
          </div>
        )
      case 1: // , ,  , 
        return (
          <div className='routeTest'><h4 className='blue'>    </h4>
            <DecideButton />
            <ScoreBoard />
            <div className='field-dora'>
              <CardList />
              <Dora />
            </div>
            <MyHand />
          </div>
        )
      case 2: //    ,   
        return (
          <div className='routeTest'>2!
            <ScoreBoard />
            <WhoseTurn /> 
            <div className='Discard-container2'>
              <MyDiscard />
              <OpponentDiscard />
            </div>
            <div className='field-dora'>
              <DecidedField />
              <Dora />
            </div>
            <Ron />
            <DecidedHand />
          </div>
        )
      default:
        return (
          <div>error</div>
        )
    }
  }


  return (
    <div className="tc">
      <Helmet>
        <meta charSet='utf-8'/>
        <style>{background}</style>
      </Helmet>
      <h1 className='title'>  17</h1>
      <div className = 'tl light-silver'> ID : {' '}{roomID}</div>
      <WaitBackdrop />
      <Result />
      <WebSocket />
      <HowToPlay />
      <Darkmode />
      {renderSwitch(phase)}

    </div>
  );
}

export default App;


